// Center block
@use "sass:math";

@mixin center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

// Column width with margin
@mixin column-width($numberColumns: 3) {
  width: map-get($columns, $numberColumns) -
    math.div($columns__margin * ($numberColumns - 1), $numberColumns);
}
